        text:
            "By the 1670s, the population of Virginia was steadily increasing. Many colonists moved deeper and deeper into the colony, where it was more expensive to transport goods, and colonists faced greater danger from surrounding Indians. Feeling vulnerable, colonists begged the royal governor of Virginia, William Berkely, for permission to exterminate the colony’s Indians. Berkley refused, and colonists became even angrier at the already corrupt leader. Under Nataniel Bacon’s leadership, the colonists revolted and slaughtered the Indians anyway. After that, the men marched into Jamestown and rebelled against Berkely. Bacon’s Rebellion put more power into the hands of farmers and poor colonists, but only weakened the power that Indians had in the colonies. Throughout the modern history of America, many groups have made massive strides, but the Native Americans always seem to get the short end of the stick.",
