        text:
            "In 1675, many Indian tribes were fed up with their treatment and led separate but similar attacks against the colonists. The colonists, believing that the attacks were part of one large coordinated movement led by the Indian chief Metacom, who the colonists called King Philip, named it King Philip’s War. With guns acquired from traders, the tribes destroyed 12 towns. Unfortunately for the Indians, colonists countered the attacks by destroying Indian crops and starving the Indians. The war ultimately ended when Metacom was killed by a praying town Indian who fought for the colonists. In total, King Philip’s War killed over 1,000 colonists and over 3,000 Indians. The war had a lasting effect on the surviving Indians. Many of them fled north to the French colony, and often sought revenge by raiding New England colonies.",
