        text:
            "The result of the French and Indian war was a terrible loss for the Indians. The British cut off delivery of goods to the Indians, affecting tribes from western Pennsylvania and Virginia. Hoping to weaken British control and lure the French back to the colonies, an Ottawa chief organized an attack on Detroit that would become known as Pontiac’s Rebellion. The Indians failed to capture the three largest and strongest British posts, and eventually ran out of supplies since their supply lines had been cut off. Although the rebellion was a failure, it did have one silver lining: sensing that the war with the Indians was more expensive than granting them respect, the British issued the Proclamation of 1763, which ordered colonial settlers to remain east of the Appalachian Mountains. Unfortunately, British authorities had trouble enforcing the proclamation.",
