        date: new Date(1868, 3, 29),
        title: "Treaty of Fort Laramie",
        text:
            "During the 1800s, many Americans travelled westward to all parts of the country to fulfill the Manifest Destiny. Colonists used a variety of transportation methods to migrate, but wagon was probably the most popular. The government was worried that Plains Indians would attack these migrants, so they passed the Treaty of Fort Laramie to bound the Indians to territories away from the major trails. Most Indians ignored the treaty because it would mean they would have to stop pursuing buffalo that crossed the imaginary boundary.",
