        title: "Indians and the California Gold Rush",
        text:
            "In 1848, gold in California caused a mass migration to the region called the California Gold Rush. White miners wanted to discourage foreign settlement of the area for both racial reasons and because they would increase competition. In order to discourage Chinese settlement, they passed the Foreign Miners Tax which levied a heavy tax on foreign miners. White miners also terrorized and killed Native Americans in the region by the thousands. Many of the surviving Indians became workers on farms and ranches.",
